import * as React from 'react';
import { Card, Grid, Typography, Button, Paper, useTheme } from '@material-ui/core';
import useStyles from './styles';

interface Props {
    question: string,
    answer: string,
    flip: any
}

const BackCard: React.FC<Props> = ({ question, answer, flip }) => {
    const theme = useTheme();
    const classes = useStyles(theme);

    const [changeToAnswer, setAnswer] = React.useState(false);

    const revealAnswer = () => {
        setAnswer(!changeToAnswer);
    }

    return (
        <div>
            <Paper className={classes.container}>
                <Grid container justify='center' alignItems='center'>
                    <Grid item>
                        <Typography className={classes.text}> {changeToAnswer ? answer : question} </Typography>
                    </Grid>
                    <Grid item className={classes.button} >
                        <Button onClick={revealAnswer}>Show {changeToAnswer ? 'Question' : 'Answer'} </Button>
                    </Grid>
                </Grid>
            </Paper>
        </div>
    );
}

export default BackCard;