import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles(theme => ({
    container: {
        width: 200,
        height: 200,
        textAlign: 'center',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        background: "#F06543",
        [theme.breakpoints.down('lg')]: {
            width: 150,
            height: 150
        },

    },
    text: {
        color: 'white',
        [theme.breakpoints.down('md')]: {
            fontSize: 10

        }
    },
    button: {
        position: 'absolute',
        width: 200,
        background: "#F09D51",
        marginTop: 50,
        bottom: 0,
        [theme.breakpoints.down('lg')]: {
            width: 150,

        },

    }
}));


export default useStyles;