import * as React from 'react';
import { Card, Grid, Typography, Paper, useTheme } from '@material-ui/core';
import useStyles from './styles';

interface Props {
    image: string,
    num: number,
    flip: any
}

const FrontCard: React.FC<Props> = ({ image, num, flip }) => {

    const theme = useTheme();

    const classes = useStyles(theme);

    return (
        <div>
            <Paper className={classes.container} onClick={flip}>
                <img className={classes.image} src={image}></img>
                <Typography className={classes.number} variant='h1'> {num} </Typography>
            </Paper>
        </div>
    );
}

export default FrontCard;