import { makeStyles, Theme } from "@material-ui/core";

const useStyles = makeStyles(theme => ({
    container: {
        width: 200,
        height: 200,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        background: '#313638',

        [theme.breakpoints.down('lg')]: {
            width: 150,
            height: 150
        },
        [theme.breakpoints.down('md')]: {
            width: 120,
            height: 120
        }


    },
    image: {
        width: 120,
        height: 120,
        opacity: 0.7,

        [theme.breakpoints.down('lg')]: {
            width: 80,
            height: 80
        }
    },
    number: {
        position: "absolute",
        fontWeight: 600,
        color: '#E8E9EB',
        [theme.breakpoints.down('lg')]: {
            fontSize: 80
        }
    }
}));


export default useStyles;